import React, {useEffect,useState} from 'react';
import axios from 'axios';
import {RFID_API_URL, API_URL} from '../../config';
import {Table,Card,ListGroup,Button} from 'react-bootstrap';

import LaundryNavbar from './LaundryNavbar';
import Loading from '../main/LoadingPage';

export default function Sessions(props) {

    const [loggedInUser, setLoggedInUser] = useState (null);
    const [sessions, setSessions] = useState(null);

    useEffect(() => {
        axios.get(`${API_URL}/user`, {withCredentials: true})
        .then((result) => {
          setLoggedInUser(result.data)
        })
        axios.get(`${API_URL}/sessions/all`, {withCredentials: true})
        .then((sessionsResponse)=>{
            setSessions(sessionsResponse.data)
        })
    }, [])
    if(!props.loggedInUser) return(<Loading/>)
    if(!sessions) return(
    <>
        <LaundryNavbar loggedInUser={props.loggedInUser} logOut = {props.logOut}/>
        <Loading/>
    </>)
    console.log(sessions)
    return (
        <>
        <LaundryNavbar loggedInUser={props.loggedInUser} logOut = {props.logOut}/>
        {
            sessions.map((session)=>{
                return(
                    <Card style={{ margin: '20px' }}>
                    <Card.Body>
                        <Card.Title>{session.loggedInUserId.userName}</Card.Title>
                        <Card.Text>
                        {session.when}
                        </Card.Text>
                    </Card.Body>
                    </Card>
                )
            })
        }
        </>
    )
}
