import React from 'react'
import {Nav, Button, Navbar,Tooltip,OverlayTrigger} from 'react-bootstrap';
import {Link} from 'react-router-dom';
import {PUBLIC_URL} from '../../config';

import Loading from '../main/LoadingPage';

export default function LaundryNavbar(props) {
    const backgroundWaterStyle ={
        backgroundImage: `url(${PUBLIC_URL}/water-background.jpg)`, 
        backgroundSize: 'cover'
    }
    const renderTooltip = (props) => (
        <Tooltip id="button-tooltip" {...props}>
          Sin autorización
        </Tooltip>
      );
    return (
        <Navbar style={{backgroundColor:'#04c3fe'}} collapseOnSelect expand="lg" sticky="top">
        <Navbar.Brand href="/"><img src={`${PUBLIC_URL}/white-logo.png`} alt='logo' height='50'/></Navbar.Brand>
        <Navbar.Toggle aria-controls="responsive-navbar-nav" />
        <Navbar.Collapse id="responsive-navbar-nav">
            
            {
                props.loggedInUser.userType==='admin'?(
                <Nav className="mr-auto">
                <Nav.Link href="/laundry/home">Envios</Nav.Link>
                <Nav.Link href="/laundry/complexes">Complejos</Nav.Link>
                <Nav.Link href="/laundry/users">Usuarios</Nav.Link>
                <Nav.Link href="/laundry/laundries">Prendas</Nav.Link>
                <Nav.Link href="/sessions">Sesiones</Nav.Link>
                </Nav>):(
                <Nav className="mr-auto">
                <Nav.Link href="/laundry/home">Envios</Nav.Link>
                <OverlayTrigger
                    placement="bottom"
                    delay={{ show: 250, hide: 400 }}
                    overlay={renderTooltip}
                >
                <Nav.Link>Complejos</Nav.Link>
                </OverlayTrigger>
                <OverlayTrigger
                    placement="bottom"
                    delay={{ show: 250, hide: 400 }}
                    overlay={renderTooltip}
                >
                <Nav.Link>Usuarios</Nav.Link>
                </OverlayTrigger>
                <OverlayTrigger
                    placement="bottom"
                    delay={{ show: 250, hide: 400 }}
                    overlay={renderTooltip}
                >
                <Nav.Link>Prendas</Nav.Link>
                </OverlayTrigger>
                <OverlayTrigger
                    placement="bottom"
                    delay={{ show: 250, hide: 400 }}
                    overlay={renderTooltip}
                >
                <Nav.Link>Sesiones</Nav.Link>
                </OverlayTrigger>
                </Nav>

                )
            }   
            {
            props.loggedInUser? (<Nav>
            <Nav><Button onClick={props.logOut}>LogOut </Button></Nav>
            </Nav>) : (<Nav>
            <Nav><Link to={'/signin'}> <Button>Registrarse</Button></Link></Nav>
            </Nav>)
            }
            
        </Navbar.Collapse>
    </Navbar>   
    )
}
